import React from 'react';
import Tex2SVG from "react-hook-mathjax";
import './card.css';

function Card(props) {  
  return (
    <div>
      <div onClick={props.handleClick} className="card-container">
        <div className="card__header">{props.title}</div>
        <div className="card__body">
          <Tex2SVG display="inline" latex={props.formula} />
        </div>
      </div>
    </div>
  )
}

export default Card
